body {
  margin: 0;
}

.hidden {
  display: none;
}

.search-form {
  width: 100%;
  text-align: center;
  background-color: #4f5cb5;
  margin-bottom: 20px;
  padding-top: 25px;
  padding-bottom: 25px;
}

.search-input {
  height: 25px;
  width: 200px;
  border: none;
  border-radius: 5px;
  padding-left: 10px;
}

.button-submit {
  height: 28px;
  width: 80px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.button-submit:focus, .button-submit:hover {
  background-color: #e2ecd0;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1270px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  grid-auto-rows: 270px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  text-decoration: none;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 190px;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.info {
  justify-content: space-between;
  margin: 0 20px;
  display: flex;
}

.info-item {
  color: #000;
  flex-direction: column;
  align-items: center;
  gap: 7px;
  font-family: Inter, sans-serif;
  font-size: 14px;
  display: flex;
}

.grey {
  color: #717171;
}

.load-more-container {
  text-align: center;
}

.load-more {
  color: #fff;
  width: 150px;
  height: 30px;
  cursor: pointer;
  background-color: #4f5cb5;
  border: none;
  border-radius: 3px;
  margin: 20px auto;
}

.load-more:focus, .load-more:hover {
  background-color: #38427e;
}

